const screenshotBtn=document.querySelector('.screenshotBtn');
const imageList=document.querySelector('.imageList');

import "regenerator-runtime/runtime.js";
import html2canvas from 'html2canvas';

screenshotBtn.addEventListener('click',function()
{
    [...document.querySelectorAll('.page')].forEach(async function(page)
        {
            const canvas=await html2canvas(page,{scale: 2,height:page.offsetHeight,width:page.offsetWidth});
            imageList.appendChild(canvas);
        });
});

